import React, { useEffect, useState } from "react";
const ImageIsLoaded = () => {
  const [isLoaded, setIsLoaded] = useState(true);

  const handleOnLoad = () => {
    setIsLoaded(false);
  };
  useEffect(() => {
    handleOnLoad();
  }, []);

  return { isLoaded, handleOnLoad };
};
export default ImageIsLoaded;
