import React from "react";
import Placeholder from "../../images/placeholderImg.svg";
import ImageIsLoaded from "../../lib/imageIsLoaded.js";
import ButtonBookmark from "../../components/ButtonBookmark/ButtonBookmark";
import { Link } from "react-router-dom";
import s from "./CardForFilms.module.scss";
import useAuth from "../../components/providers/useAuth";

function CardForFilms({ el, index }) {
  const { isLoaded, handleOnLoad } = ImageIsLoaded();
  const { user } = useAuth();
  return (
    <>
      <div className={s.filmCard} key={index}>
        <span className={s.filmCard__rating}>{el.rating.kp.toFixed(1)}</span>
        <div className={s.filmCard__img}>
          <Link to={"/movie/" + el.id}>
            {isLoaded === false ? (
              <img onLoad={handleOnLoad} loading="lazy" src={el.poster ? el.poster.previewUrl : ""} alt={el.name} />
            ) : (
              <img src={Placeholder} alt="Картинка"></img>
            )}
          </Link>
        </div>
        <div className={s.filmCard__info}>
          <Link to={"/movie/" + el.id}>
            <h2 className={s.filmCard__name}>{el.name}</h2>
            <div className={s.filmCard__length}>{el.movieLength ? `${el.year}, ${el.movieLength}мин` : el.year}</div>
          </Link>
          {user && <ButtonBookmark el={el}></ButtonBookmark>}
        </div>
      </div>
    </>
  );
}
export default CardForFilms;
